import { useState } from "react";
import Cleave from "cleave.js/react";
import onSubmitForm from "../Api/sendMessage";

const Modal = ({ onClickOpenModal }) => {
  const [numberInput, setNumberInput] = useState("+998 ");
  const [nameInput, setNameInput] = useState("");
  const [courseSelect, setCourseSelect] = useState(undefined);
  const [nameInputValid, setNameInputValid] = useState({
    valid: false,
    value: "",
    sendValid: "",
  });
  const [numberInputValid, setNumberInputValid] = useState({
    valid: false,
    value: "",
    sendValid: "",
  });
  const [courseSelectValid, setCourseSelectValid] = useState({
    valid: false,
    value: "",
    sendValid: "",
  });

  const [btnDis, setBtnDis] = useState(false);

  const onBlurValidName = (e) => {
    const nameValue = e.target.value;
    const target = e.target.style;

    if (/\d/gi.test(nameValue)) {
      setNameInputValid({
        valid: true,
        value: "Son yozmay ism yozing!",
        sendValid: false,
      });
      target.outline = "2px solid red";
    } else if (nameValue.length === 0) {
      setNameInputValid({
        valid: true,
        value: "Ismingizni yozing!",
        sendValid: false,
      });
      target.outline = "2px solid red";
    } else if (/\w/gi.test(nameValue)) {
      const arr = nameValue.match(/\w/gi);
      const value1 = arr[0];
      let count = 0;
      arr.forEach((item) => {
        if (value1.toLowerCase() === item.toLowerCase()) {
          count++;
        }
      });
      if (arr.length === count || nameValue.length < 3) {
        setNameInputValid({
          valid: true,
          value: "Iltimos ismingizni yozing!",
          sendValid: false,
        });
        target.outline = "2px solid red";
      } else {
        setNameInputValid({ valid: false, value: "", sendValid: true });
        target.outline = "none";
      }
    }
  };

  const onBlurValidNumber = (e) => {
    const nameValue = e.target.value;
    const target = e.target.style;
    if (nameValue.length !== 19) {
      setNumberInputValid({
        valid: true,
        value: "Raqamni to'liq yozing!",
        sendValid: false,
      });
      target.outline = "2px solid red";
    } else {
      setNumberInputValid({ valid: false, value: "", sendValid: true });
      target.outline = "none";
    }
  };

  const onBlurValidCourse = (e) => {
    const nameValue = e.target.value;
    const target = e.target.style;
    if (nameValue === "Kursni tanlang") {
      setCourseSelectValid({
        valid: true,
        value: "Kursni tanlang!",
        sendValid: false,
      });
      target.outline = "2px solid red";
    } else {
      setCourseSelectValid({ valid: false, value: "", sendValid: true });
      target.outline = "none";
    }
  };

  const onClickButton = (e) => {
    const name = e.target.form[0];
    const number = e.target.form[1];
    const course = e.target.form[2];
    if (!nameInputValid.sendValid) {
      name.style.outline = "2px solid red";
      setNameInputValid({
        valid: true,
        value: "Ismingizni yozing!",
        sendValid: false,
      });
    } else name.style.outline = "none";

    if (!numberInputValid.sendValid) {
      number.style.outline = "2px solid red";
      setNumberInputValid({
        valid: true,
        value: "Raqamni to'liq yozing!",
        sendValid: false,
      });
    } else number.style.outline = "none";

    if (!courseSelectValid.sendValid) {
      course.style.outline = "2px solid red";
      setCourseSelectValid({
        valid: true,
        value: "Kursni tanlang!",
        sendValid: false,
      });
    } else course.style.outline = "none";

    if (!nameInputValid.sendValid) {
      name.focus();
    } else if (!numberInputValid.sendValid) {
      number.focus();
    } else if (!courseSelectValid.sendValid) {
      course.focus();
    }
  };

  const onHandleChangeInput = (e) => {
    if (e.target.name === "numberInput") {
      setNumberInput(e.target.value);
      onBlurValidNumber(e);
    } else if (e.target.name === "nameInput") {
      setNameInput(e.target.value);
      onBlurValidName(e);
    } else if (e.target.name === "courseSelect") {
      setCourseSelect(e.target.value);
      onBlurValidCourse(e);
    }
  };

  const clearInput = () => {
    setNumberInput("+998 ");
    setNameInput("");
    setCourseSelect(undefined);
    setNameInputValid({
      valid: false,
      value: "",
      sendValid: "",
    });
    setNumberInputValid({
      valid: false,
      value: "",
      sendValid: "",
    });
    setCourseSelectValid({
      valid: false,
      value: "",
      sendValid: "",
    });
  };

  return (
    <>
      <div className="modals">
        <button className="close-modal" onClick={() => onClickOpenModal()}>
          &times;
        </button>
        <form
          onSubmit={(e) =>
            onSubmitForm(
              e,
              onClickOpenModal,
              clearInput,
              nameInputValid.sendValid &&
                numberInputValid.sendValid &&
                courseSelectValid.sendValid,
              setBtnDis
            )
          }
        >
          <div>
            <label htmlFor="nameInputCM">
              <input
                type="text"
                name="nameInput"
                placeholder="Ism va familyangiz"
                autoComplete="off"
                id="nameInputCM"
                value={nameInput}
                onChange={onHandleChangeInput}
                onBlur={onBlurValidName}
              />
            </label>
            <div className="text-end">
              {nameInputValid.valid ? <span>{nameInputValid.value}</span> : ""}
            </div>
          </div>
          <div>
            <label htmlFor="numberInputCM">
              <Cleave
                options={{
                  blocks: [4, 0, 2, 0, 3, 2, 2],
                  delimiters: [" ", "(", ")", " ", "-"],
                  numericOnly: true,
                  prefix: "+998",
                }}
                value={numberInput}
                onChange={onHandleChangeInput}
                name="numberInput"
                autoComplete="off"
                type="tel"
                id="numberInputCM"
                onBlur={onBlurValidNumber}
              />
            </label>
            <div className="text-end">
              {numberInputValid.valid ? (
                <span>{numberInputValid.value}</span>
              ) : (
                ""
              )}
            </div>
          </div>
          <div>
            <label htmlFor="selectCourse">
              <select
                name="courseSelect"
                id="selectCourse"
                onChange={onHandleChangeInput}
                onBlur={onBlurValidCourse}
                value={courseSelect}
              >
                <option value={undefined}>Kursni tanlang</option>
                <option value="Kompyuter savodxonligi">
                  Kompyuter savodxonligi
                </option>
                <option value="Frontend dasturlash">Frontend dasturlash</option>
                <option value="IT English">IT English</option>
              </select>
            </label>
            <div className="text-end">
              {courseSelectValid.valid ? (
                <span>{courseSelectValid.value}</span>
              ) : (
                ""
              )}
            </div>
          </div>
          <div>
            <button type="submit" onClick={onClickButton} disabled={btnDis}>
              Yuborish
            </button>
          </div>
        </form>
      </div>
      <div className="overlay" onClick={() => onClickOpenModal()}></div>
    </>
  );
};

export default Modal;
