import { toast } from "react-toastify";
import { Axios } from "../../config/Axios";

const onSubmitForm = (e, onClickOpenModal, clearInput, valid, setBtnDis) => {
  e.preventDefault();
  setBtnDis(true);

  let name = e.target[0].value;
  let number = e.target[1].value
    .replace(/\(/g, "")
    .replace(/\)/g, "")
    .replace(/-/g, "")
    .replace(/ /g, "");
  let course = e.target[2].value;
  let message = `Ismi: ${name}\nTelefon raqami: ${number}\nKurs: ${
    course ? course : "Qiziqish"
  }`;

  if (valid) {
    if (onClickOpenModal) onClickOpenModal();
    if (clearInput) clearInput();
    
    Axios.post("/api/sendMessage", { message })
      .then(({ data }) => {
        toast.success(data, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      })
      .catch((err) => {
        toast.error(
          `${
            err
              ? err.response.data
              : "Xato! Serverda xatolik, iltimos keyinroq boshqatdan urunib ko'ring!"
          }`,
          {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          }
        );
      })
      .finally(() => {
        setBtnDis(false);
      });
  }
};

export default onSubmitForm;
